import React from 'react';
import InfoIcon from './InfoIcon';
import Loading from './Loading';

export default function DescriptionBlock(props){
    
    const meta = props.meta;
    return(
    <div className="content-element description-block">
        <h3>{props.title ? props.title : <Loading />}</h3>
    {!meta ? "" : Object.keys(meta).map((el, i)=><InfoIcon name={el} key={i} divClassName="info-icon">{meta[el]}</InfoIcon>)}
        {props.children}
    </div>
    );
}